import Dropdown from "./Dropdown.vue";

export default {
  props: [
    "index",
    "name",
    "label",
    "category",
    "type",
    "module_name",
    "link",
    "count",
    "onboard_present",
    "links",
    "description",
    "hidden",
    "icon"
  ],
  components: {
    Dropdown
  },
  data() {
    return {
      hovered: 0
    };
  },
  computed: {
    iconClass() {
      if (this.icon) {
        return this.icon;
      } else {
        return "octicon octicon-file-text";
      }
	},
	dropdown_links() {
		return this.links.length > 0 ? this.links
			.filter(link => !link.hidden)
			.concat([
				{ label: __('Customize'), action: () => this.$emit('customize'), class: 'border-top' }
			]) : [];
	}
  },
};
