import Popover from "./Popover.vue";

export default {
  name: "Dropdown",
  components: {
    Popover
  },
  props: {
    items: {
      type: Array,
      default: () => []
    },
    label: {
      type: String,
      default: "Dropdown"
    },
    align: {
      type: String,
      default: "right"
    }
  },
  data() {
    return {
      isOpen: false
    };
  },
  computed: {
    dropdownItems() {
      return (this.items || []).map(item => {
        if (typeof item === "string") {
          return {
            label: item,
            action: console.log
          };
        }
        if (!item.action && item.route) {
          item.action = this.setRoute.bind(this, item.route);
        }
        return item;
      });
    }
  },
  methods: {
    setRoute(route) {
      this.$router.push(route);
    }
  }
};
